import React from 'react'
import Calendar from '../components/Calendar'
import "./Popup.css"
const Popup = () => {
    return (
        <div className='popup__container'>
            <div className="popup__title">
                <h1>Calendario 5to Semestre</h1>
            </div>
            <Calendar />
        </div>
    )
}

export default Popup