import React from 'react'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import "./Calendar.css"
const MySwal = withReactContent(Swal)

const Calendar = () => {
    const materias = {
        CG: "cg",
        IA: "https://cedia.zoom.us/j/4719244427",
        R2: "r2",
        IS: "https://cedia.zoom.us/j/5726508437",
        BDD: "bdd"
    }
    const dias = ["Lunes", "Martes", "Miercoles", "Jueves", "Viernes", "Sabado", "Domingo"]
    const hours = ["None", "7-8", "8-9", "9-10", "10-11", "11-12", "12-13", "13-14", "14-15", "15-16", "16-17", "17-18", "18-19", "19-20"]
    const materias1 = ["Lunes", "CG", "CG", "CG", "IA", "IA", "IA", "None", "None", "None", "None", "None", "None", "None"]
    const materias2 = ["Martes", "CG", "CG", "IA", "IA", "None", "None", "None", "None", "None", "None", "None", "BDD", "BDD"]
    const materias3 = ["Miercoles", "R2", "R2", "R2", "IS", "IS", "IS", "None", "None", "None", "None", "None", "None", "None"]
    const materias4 = ["Jueves", "None", "None", "None", "None", "None", "None", "None", "None", "None", "None", "None", "None", "None"]
    const materias5 = ["Viernes", "None", "R2", "R2", "IS", "IS", "None", "None", "None", "None", "None", "None", "None", "None"]
    const materias6 = ["Sabado", "None", "None", "BDD", "BDD", "BDD", "None", "None", "None", "None", "None", "None", "None", "None"]
    const whitchClass = (materia) => {
        for (const materiaAux in materias) {
            if (materia === String(materiaAux)) return materias[materiaAux]
        }
        return false
    }
    const handleFunction = (e, materia) => {
        e.preventDefault()
        const materiaAux = whitchClass(materia)
        if (materiaAux === false) { return console.log("No es una materia!") }
        console.log(materiaAux)
        const validateLink = /^http/.test(materiaAux);
        if (validateLink) {
            window.open(materiaAux, '_blank');
        } else {
            MySwal.fire({
                icon: 'error',
                title: 'Oops...',
                text: 'La clase no tiene link de zoom!',
                footer: 'Prueba en Teams!',
                background: "#000000",
            })
        }
    }
    return (
        <div className='calendar__container'>
            <div className="col col1">
                {hours.map((hour, key) => <div id={hour === 'None' ? "item0" : ""} className='item hour' key={key}>{hour}</div>)}
            </div>
            <div className="col col2">
                {materias1.map((materia, key) => <div onClick={(e) => (handleFunction(e, materia))} id={materia === 'None' ? "bg-empty" : dias.includes(materia) ? "day" : "clicleable"} className={'item '+materia } key={key}>{materia}</div>)}
            </div>
            <div className="col col3">
                {materias2.map((materia, key) => <div onClick={(e) => (handleFunction(e, materia))} id={materia === 'None' ? "bg-empty" : dias.includes(materia) ? "day" : "clicleable"} className={'item '+materia } key={key}>{materia}</div>)}
            </div>
            <div className="col col4">
                {materias3.map((materia, key) => <div onClick={(e) => (handleFunction(e, materia))} id={materia === 'None' ? "bg-empty" : dias.includes(materia) ? "day" : "clicleable"} className={'item '+materia } key={key}>{materia}</div>)}
            </div>
            <div className="col col5">
                {materias4.map((materia, key) => <div onClick={(e) => (handleFunction(e, materia))} id={materia === 'None' ? "bg-empty" : dias.includes(materia) ? "day" : "clicleable"} className={'item '+materia } key={key}>{materia}</div>)}
            </div>
            <div className="col col6">
                {materias5.map((materia, key) => <div onClick={(e) => (handleFunction(e, materia))} id={materia === 'None' ? "bg-empty" : dias.includes(materia) ? "day" : "clicleable"} className={'item '+materia } key={key}>{materia}</div>)}
            </div>
            <div className="col col7">
                {materias6.map((materia, key) => <div onClick={(e) => (handleFunction(e, materia))} id={materia === 'None' ? "bg-empty" : dias.includes(materia) ? "day" : "clicleable"} className={'item '+materia } key={key}>{materia}</div>)}
            </div>

        </div>
    )
}

export default Calendar